const deleteStyle = {
  marginLeft: 55,
  display: "block",
  borderRadius: 50,
  marginTop: -0,
  fontSize: "12px",
  color: "pink"
}
const itemWrapper = {
  display: "flex"
}
const List = props => (
  //go to the browser
  <ul>
    {
      props.items.map((item, index) => <li key={index} style={itemWrapper}><div>{item}</div><span style={deleteStyle} onClick={()=> props.deleteItem(index, item)}>X</span></li>)
    }
  </ul>
);

export default List;